import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-payment',
  templateUrl: './payment.component.html',
  styleUrls: ['./payment.component.css']
})
export class PaymentComponent {
  paymentMethods: string[] = ['Credit Card', 'PayPal', 'Bank Transfer'];

  selectedPaymentMethod: string = '';
  selectedBookingType: string = ''; // Assuming you have this to control required fields
  cardNumber: string = '';
  holderName: string = '';
  ccv: string = '';
  expiryDate: string = '';
  paypalEmail: string = '';
  bankName: string = '';
  accountNumber: string = '';
  ifscCode: string = '';
  minExpiryDate: string = new Date().toISOString().split('T')[0]; // Minimum expiry date is today

  handleFormSubmit(form: NgForm) {
    if (form.valid) {
      // Handle form submission
      console.log('Form Submitted:', form.value);
      // Add your form submission logic here, e.g., sending data to a server
    } else {
      console.log('Form is invalid');
    }
  }
}
